export default {
  props: {
    chapter: {
      type: Array,
      default: function _default() {
        return [];
      }
    }
  },
  name: 'Chapter',
  data: function data() {
    return {
      sectionIndex: 0
    };
  },
  methods: {
    // 获取专栏文章详情
    getSection: function getSection(id) {
      window.scroll(0, 0);
